@import "variables.scss";
@import "projectImageCarousel.scss";

.modal_wholePage {
  @include respond {
    height: 100vh;
  }
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  background-color: colorBlack(40);
  width: 100%;
  height: 100%;
  backdrop-filter: blur(3.5px);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  place-content: center;
}
.modal_container {
  @include responsiveSize(1000, 600);
  @include respond {
    width: 95vw;
    height: 60vh;
  }
  display: flex;
  flex-direction: column;
  // position: absolute;
  // top: 50%;
  // left: 50%;
  z-index: 10;
  // transform: translate(-50%, -50%);
  transform: scale(0.8);
  transition: transform 0.3s ease;
  > .modal_container_topBox {
    @include respond {
      width: 100%;
      height: 6%;
      padding-right: 2%;
      padding-left: 2%;
    }
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: colorWhite(70);
    background: colorBlack(40);
    padding: 0 1%;
    width: 100%;
    height: 8%;
    border-bottom: 3px solid colorA(80);
    > .modal_topBox_left {
      @include respond {
        justify-content: start;
        width: 28%;
      }
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 1%;
      width: 18%;
      height: 60%;
      transition: color 0.2s ease;
      > span {
        @include responsiveFontSize(17);
        @include respond {
          margin-right: 5%;
        }
        font-family: "Montserrat-Medium";
        cursor: pointer !important;
      }
      > .modal_icon_link {
        @include responsiveFontSize(15);
        @include respond {
        }
      }
      &:hover {
        color: colorWhite(100);
      }
    }
    > .modal_topBox_right {
      @include responsiveFontSize(30);
      @include respond {
        @include responsiveFontSize(25);
        @include responsiveSize(45, 45);
        display: grid;
        place-content: center;
      }
      transition: color 0.2s ease;
      &:hover {
        color: colorWhite(100);
      }
    }
  }
  > .modal_container_bottomBox {
    @include respond {
      flex-direction: column;
      justify-content: start;
    }
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: colorBlack(100);
    background: colorWhite(70);
    width: 100%;
    height: 92%;

    > .modal_conatiner_bottom_leftBox {
      @include respond {
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 50%;
      }
      display: flex;
      flex-direction: column;
      width: 60%;
      height: 100%;
      > .modal_bottom_left_imgBox {
        //왼쪽 위 이미지 캐러셀 박스
        @include responsiveSize(600, 337.5);
        @include respond {
          width: 55%;
          height: 100%;
        }
        display: flex;
        justify-content: center;
        position: relative;
        overflow: hidden;
      }
      > .modal_bottom_left_summaryBox {
        //왼쪽 아래 제목, 설명 박스
        @include responsiveSize(600, 214.5);
        @include respond {
          justify-content: start;
          align-items: center;
          padding: 0;
          width: 45%;
          height: 100%;
        }
        display: flex;
        flex-direction: column;
        position: relative;
        background-color: colorWhite(60);
        padding: 18px;

        > .modal_summaryBox_topBox {
          //제목 박스
          @include respond {
            justify-content: space-around;
            width: 90%;
            height: 45%;
            padding-top: 5%;
          }
          display: flex;
          flex-direction: column;
          justify-content: end;
          align-items: start;
          width: 100%;
          height: 50%;
          padding-bottom: 10px;
          border-bottom: 2px solid colorBlack(15);
          > .summaryBox_title {
            @include respond {
              flex-direction: column;
            }
            @include responsiveFontSize(30);
            display: flex;
            flex-direction: row;
            color: colorBlack(90);
            width: 100%;
            //프로젝트 제목
            cursor: default;
            font-family: "Pretendard-Black";
            > .modal_summaryBox_prize_container {
              @include respond {
                width: 100%;
              }
              display: flex;
              flex-direction: row;
              justify-content: start;
              align-items: center;
              width: 50%;
              height: 100%;
              > .prize_text {
                @include respond {
                  @include responsiveFontSize(19);
                  color: colorA(100);
                  visibility: visible;
                }
                @include responsiveFontSize(17);
                font-family: "Pretendard-Regular";
                visibility: hidden;
                vertical-align: middle;
                cursor: default;
              }
              &:hover {
                > .prize_text {
                  color: colorA(100);
                  visibility: visible;
                }
                > .prize_icon {
                  color: colorA(100);
                }
              }
              > .prize_icon {
                @include respond {
                  display: none;
                }
                @include responsiveSize(27, 27);
                color: colorA(50);
                margin: 0 3% 0 5%;
              }
            }
          }
          > span {
            //프로젝트 간략 소개
            @include responsiveFontSize(16);
            @include respond {
              @include responsiveFontSize(18);
            }
            color: colorBlack(50);
            font-family: "Pretendard-Bold";
          }
        }
        > .modal_summaryBox_bottomBox {
          //설명 박스
          @include respond {
            height: 50%;
            padding-left: 5%;
            padding-right: 5%;
          }
          display: flex;
          flex-direction: column;
          justify-content: start;
          width: 100%;
          height: 50%;
          padding-right: 20px;
          padding-top: 10px;
          > span {
            @include responsiveFontSize(16);
            @include respond {
              @include responsiveFontSize(18);
            }
            color: colorBlack(100);
            font-family: "Pretendard-Light";
          }
        }
      }
    }
    > .modal_conatiner_bottom_rightBox {
      @include respond {
        width: 100%;
        height: 55%;
      }
      background-color: colorWhite(80);
      padding: 15px;
      width: 40%;
      height: 100%;
      overflow: hidden;
      cursor: default;
      // ::-webkit-scrollbar {
      //   display: none;
      // }
      > .modal_rightBox_Info {
        width: 100%;
        height: 100%;
        overflow-y: scroll;
        padding-right: 7%;
        > div {
          //각 항목별 큰 박스
          width: 100%;
          height: fit-content;
          border-bottom: 2px dashed colorBlack(15);
          padding-top: 20px;
          padding-bottom: 20px;
        }
        > div:first-child {
          //첫번째 박스
          padding-top: 0;
        }
        > div:last-child {
          //마지막 박스
          border-bottom: none;
        }
        > .modal_rightBox_info {
          > ul > li {
            div:first-child {
              @include responsiveFontSize(18);
              @include responsiveFontSize(21);
              font-family: "Pretendard-Medium";
            }
            div:last-child {
              @include responsiveFontSize(15);
              @include respond {
                @include responsiveFontSize(18);
              }
            }
          }
        }
        > div > h1 {
          @include responsiveFontSize(22);
          @include respond {
            @include responsiveFontSize(25);
          }
          //담당 기능/강화역량/활용기술/수행정보
          // color: $colorA;
          margin: 8px 0px;
          font-family: "Pretendard-Bold";
        }
        > div > ul > ul {
          //활용기술 내부
          margin-left: 10px;
        }
        > div > ul > ul > h2 {
          //Front-End, Back-End, Others
          @include responsiveFontSize(18);
          @include respond {
            @include responsiveFontSize(21);
          }
          font-family: "Pretendard-SemiBold";
          margin-bottom: 5px;
        }
        > div li {
          //li들
          @include responsiveFontSize(16);
          @include respond {
            @include responsiveFontSize(19);
          }
          font-family: "Pretendard-Light";
          margin-left: 30px;
          margin-bottom: 8px;
          list-style: disc;
        }
        .gain_big {
          //강화 역량 타이틀
          @include responsiveFontSize(18);
          @include respond {
            @include responsiveFontSize(21);
          }
          font-family: "Pretendard-Medium";
        }
        .gain_small {
          //강화 역량 내용
        }
      }
    }
  }
}

.popUp_wholePage {
  opacity: 1;
  pointer-events: all;
  > .modal_container {
    transform: scale(1);
  }
}
